body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.item-container {
    padding: 20px;
    flex-basis: 100px;
    margin-bottom: 20px;
    position: relative;
}

.comments-container {
    padding-left: 15px;
}

.title-container {
    padding-top: 15px;
    word-wrap: break-word;
}

.delete-item-btn {
    position: absolute;
    top: 0;
    right: 0;
}

.new-item-container {
    position: fixed;
    top: 0;
    width: 100vw;
    height: 60px;
    display: flex;
    justify-content: center;
    align-items: flex-end;
    background-color: rgba(219, 217, 217, 0.95);
    z-index: 10;
    padding-bottom: 20px;
}

.container-items {
    margin: 80px auto;
    padding: 20px;
    display: flex;
    flex-direction: column;
    max-width: 600px;
}
